import React, { Component, useEffect, useState } from "react";
import { CheckCircle } from "react-bootstrap-icons";
import { Link } from "react-router-dom";

export default class Dashboard extends Component {

    constructor(props) {
        super(props);
        this.state = {
            offers: localStorage.getItem('offers') ? JSON.parse(localStorage.getItem('offers')) : []
        }
    };

    render() {
        const offers = this.state.offers.map((item, i) => {
            return <Link to={`/detail/${item.offer}`} className="list-group-item list-group-item-action" key={i}>
                <CheckCircle className="icon"/>
                <span>{item.beacon}</span>
            </Link>;  
        });

        console.log(offers.length);

        return (
            <React.Fragment>
                <img className="logo" src="logo-black.png" alt=""/>
                 <div className="detail">
                <h3>DASHBOARD</h3>
                <div>
                    <div className="container">
                        <div className="row background-white">
                            <div className="col-md-12 title-wrapper">
                                <div className="float-left title">
                                    <h2>List Offer</h2>
                                </div>
                                <div className="float-right">
                                <Link className="btn btn-primary btn-block btn-default btn-default btn-add" to="/detail" >ADD</Link>
                            </div>
                                
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-12 background-white dashboard-list">
                                <div className="content">
                                    <div className="list-group">
                                        {offers}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </React.Fragment>
        );
    }
}