import React, { Component } from "react";

const DASHBOARD = '/dashboard';
export default class OfferDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            beacon: '',
            distance: '',
            offer: '',
            timeStay: '',
        }
    }

    beaconChange = (event) => {
        this.setState({beacon: event.target.value});
      }

    onDistanceChange = (event) => {
        this.setState({ distance: event.target.value });
    }

    onOfferChange = (event) => {
        this.setState({ offer: event.target.value });
    }

    onTimeStayChange = (event) => {
        this.setState({ timeStay: event.target.value });
    }

    handleOfferSubmit = () => {
        const offer = {
            beacon: this.state.beacon,
            distance: this.state.distance,
            offer: this.state.offer,
            timeStay: this.state.offer,
        };
        const offers = localStorage.getItem('offers') ? JSON.parse(localStorage.getItem('offers')) : [];

        offers.push(offer);

        localStorage.setItem('offers', JSON.stringify(offers));
        window.location.assign(DASHBOARD);
    }

    render() {
        return (
            <React.Fragment>
                <img className="logo" src="logo-black.png" alt=""/>
                <div className="detail">
                    <div className="container">
                        <div className="col-sm">
                        <h3>OFFER DETAIL</h3>
                            <div className="detail-content">
                            <form>
                                <div className="form-group">
                                    <label>Beacon</label>
                                    <select 
                                        value={this.state.beacon}
                                        className="beacon txtCombobox"
                                        onChange={this.beaconChange}
                                        >
                                        <option value="Please choice Store" selected>Please choice a store</option>
                                        <option value="ADIDAS">ADIDAS</option>
                                        <option value="ZARA">ZARA</option>
                                        <option value="TOYS ARE US">TOYS ARE US</option>
                                    </select>
                                </div>
                                <div className="form-group distance">
                                    <label>Distance</label>
                                    <input type="textbox"
                                        onChange={this.onDistanceChange}
                                    ></input>
                                    <span>&nbsp; (m)</span>
                                </div>
                            
                                <div className="form-group">
                                    <label>Offer</label>
                                    <input type="textbox txtOffer"
                                        onChange={this.onOfferChange}
                                    ></input>
                                </div>
                                <div className="form-group">
                                    <label>Time To Stay</label>
                                    <input type="textbox txtOffer"
                                        onChange={this.onTimeStayChange}
                                    ></input>
                                    <span>&nbsp; (sec)</span> 
                                </div>
                                </form>
                                <button
                                    className="btn btn-primary btn-block btn-default btn-default btn-save"
                                    type="button"
                                    onClick={this.handleOfferSubmit}
                                >
                                    SAVE
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}